import { Component } from "@angular/core";
import { inject } from "@angular/core/src/render3";
import { MenuController } from "ionic-angular";
import { ModalController } from "ionic-angular";
import { Livre } from "../../models/Livre";
import { LivresService } from "../../services/books.service";
import { SingleBookPage } from "./single-book/single-book";


@Component({
  selector: "page-books",
  templateUrl: "home.html",
  styleUrls: ['home.scss'],
})
export class HomePage {
  livresList: Livre[];

  constructor(
    private modalCtrl: ModalController,
    private livreService: LivresService,
    private menuCtrl: MenuController
  ) {}

  ionViewWillEnter() {
    this.livresList = this.livreService.livresList.slice();
  }

  onLoadLivre(index: number) {
    let modal = this.modalCtrl.create(SingleBookPage, {
      index: index,
    });
    modal.present();
  }

  onToggleMenu() {
    this.menuCtrl.open();
  }
}



